<template>
  <div>
    <b-form @submit.prevent="submit">
      <b-row class="d-flex align-items-center">
        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{
            $t("CORRESPONDINGTYPE")
          }}</label
          >&nbsp;&nbsp;
          <b-button
            variant="primary"
            class="px-2 required-btnAny align-content-center"
            >{{ $t("REQUIREDBUTTON") }}</b-button
          >
        </b-col>
        <b-col cols="12" xl="2">
          <b-form-select
            v-model="messageForm.type"
            :plain="true"
            :options="correspondingTypeOptions"
          ></b-form-select>
          <show-errors
            :control="$v.messageForm.type"
            :field="$t('CORRESPONDINGTYPE')"
          />
        </b-col>

        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{
            $t("CUSTOMERRESPONSEDATE")
          }}</label
          >&nbsp;&nbsp;
          <b-button
            variant="primary"
            class="px-2 required-btnAny align-content-center"
            >{{ $t("REQUIREDBUTTON") }}</b-button
          >
        </b-col>
        <b-col cols="12" xl="2">
          <DatePicker
            v-model.trim="$v.messageForm.customer_response_date.$model"
            input-class="form-control"
            format="YYYY/MM/DD hh:mm"
            type="datetime"
          ></DatePicker>
          <show-errors
            :control="$v.messageForm.customer_response_date"
            :field="$t('CUSTOMERRESPONSEDATE')"
          />
        </b-col>

        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{ $t("ACTION") }}</label
          >&nbsp;&nbsp;
          <b-button
            variant="primary"
            class="px-2 required-btnAny align-content-center"
            >{{ $t("REQUIREDBUTTON") }}</b-button
          >
        </b-col>
        <b-col cols="12" xl="2">
          <b-form-select
            :plain="true"
            v-model="messageForm.action"
            :options="actionOptions"
            class="wdtAlign"
          ></b-form-select>
          <show-errors :control="$v.messageForm.action" :field="$t('ACTION')" />
        </b-col>
      </b-row>
      <br />
      <b-row class="d-flex align-items-center">
        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{ $t("MESSAGETITLE") }}</label
          >&nbsp;
          <b-button
            variant="primary"
            class="px-2 required-btnAny align-content-center"
            >{{ $t("REQUIREDBUTTON") }}</b-button
          >
        </b-col>
        <b-col cols="12" xl="10">
          <b-form-input
            v-model.trim="$v.messageForm.title.$model"
            class="form-control"
          ></b-form-input>
          <show-errors
            :control="$v.messageForm.title"
            :field="$t('MESSAGETITLE')"
          />
        </b-col>
      </b-row>
      <br />
      <b-row class="d-flex">
        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{ $t("MAILCONTENT") }}</label
          >&nbsp;&nbsp;
          <b-button
            variant="primary"
            class="px-2 required-btnAny align-content-center"
            >{{ $t("REQUIREDBUTTON") }}</b-button
          >
        </b-col>
        <b-col cols="12" xl="10">
          <b-form-textarea
            id="textarea-rows"
            v-model.trim="$v.messageForm.content.$model"
            class="mb-1"
            rows="8"
          ></b-form-textarea>
          <show-errors
            :control="$v.messageForm.content"
            :field="$t('MAILCONTENT')"
          />
        </b-col>
      </b-row>
      <br />
      <p class="text-danger">{{ fileErrorMessage }}</p>
      <b-row class="d-flex align-items-center">
        <b-col cols="12" xl="2"> </b-col>
        <b-col cols="12" xl="10">
          <div class="border border-dark pl-4 pr-4">
            <b-row class="pt-2 pb-2">
              <b-col xl="3">
                <b-button id="fileSelectBtn" variant="primary" class="mb-2">
                  <label for="avatar">{{ $t("FILESELECTION") }}</label>
                </b-button>
                <input
                  id="avatar"
                  @change="onFileChange"
                  type="file"
                  name="avatar"
                  multiple="multiple"
                  hidden
                />
              </b-col>
              <b-col xl="8" class="border border-dark pl-4 pr-4 ml-2">
                <b-card-text>
                  <div v-if="files.length">
                    <div v-for="(file, index) in files" v-bind:key="index">
                      {{ file.name }}
                      <b-link
                        @click="deleteFile(index)"
                        href="javascript:;"
                        class="card-link link"
                      >
                        <u>{{ $t("DELETE") }}</u>
                      </b-link>
                    </div>
                  </div>
                  <div v-else></div>
                </b-card-text>
              </b-col>
            </b-row>
          </div>
        </b-col>
      </b-row>
      <b-button
        :disabled="disabled"
        variant="primary"
        class="mt-2"
        type="submit"
        >{{ $t("CUSTOMERSUPPORTREGISTRATION") }}
        <i v-if="loading" class="fa fa-spinner fa-spin"
      /></b-button>
      <br /><br />
    </b-form>
  </div>
</template>

<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import i18n from "@/plugins/i18n";
import ShowErrors from "@/components/ShowErrors";
import { required } from "vuelidate/lib/validators";
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";

const defaultForm = {
  type: "001",
  customer_response_date: "",
  action: "001",
  title: "",
  content: "",
};
export default {
  name: "ClientTransactionAdd",
  components: { ShowErrors },
  data() {
    return {
      messageForm: {
        type: "001",
        customer_response_date: "",
        action: "001",
        title: "",
        content: "",
      },
      files: [],
      fileErrorMessage: "",
      loading: false,
      disabled: false,
      message_type: "005",
      message_sender_account_type: "",
      selectedClientIcon : "",
      clientBaseInfo : {},
      senderBelongs: "",
      senderIconPath: ""
    };
  },
  validations: {
    messageForm: {
      type: { required },
      customer_response_date: { required },
      action: { required },
      title: { required },
      content: { required },
    },
  },
  computed: {
    ...mapGetters({
      client_details: "getUserDetails",
      selected_client_data: "getClientData",
      PayLoad: "getQuotationNo",
      assessment_Details: "getAssesmentNo",
    }),
    correspondingTypeOptions() {
      return [
        { text: i18n.t("TELEPHONE"), value: "001" },
        { text: i18n.t("FAX"), value: "002" },
        { text: i18n.t("EMAIL1"), value: "003" },
        { text: i18n.t("FACETOFACE"), value: "004" },
        { text: i18n.t("MEMOPERSONINCHANGE"), value: "005" },
        { text: i18n.t("MESSAGETRANSMISSION"), value: "006" },
      ];
    },
    actionOptions() {
      return [
        { text: i18n.t("CONTACTFROMCUSTOMER"), value: "001" },
        { text: i18n.t("CONTACTFROMPERSONINCHARGE"), value: "002" },
        { text: i18n.t("SENDMESSAGETOCUSTOMER"), value: "003" },
        { text: i18n.t("OTHER"), value: "004" },
      ];
    },
  },
  mounted(){
    this.getClientIconPath();
    this.getSelectedClientData();
  },
  created() {
    if (!this.selected_client_data.client_no) {
      this.$router.push({ name: "client_list" });
    }
  },
  methods: {
    /**
     * @description function to get the client icon Path
     * @name getClientIconPath
     */ 
    async getClientIconPath(){
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTOTHERINFO,
        {
          fieldlist: ["client_no", "icon_path"],
          conditions: [
            {
              key: "client_no",
              value: this.selected_client_data.client_no,
            },
          ],
        });
        if(data.success){
          let responseData = data.data;
          if(responseData.length > 0){
            this.selectedClientIcon = responseData[0].icon_path ? responseData[0].icon_path : "";
          }
        }
    },
    async getSelectedClientData(){
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTBASEINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: this.selected_client_data.client_no,
            },
          ],
        });
        if(data.success){
          let responseData = data.data;
          if(responseData.length > 0){
            this.clientBaseInfo = responseData[0] ? responseData[0] : "";
          }
        }
    },
    /**
     * @description function to upload the file
     * @name onFileChange
     */      
    onFileChange(event) {
      this.fileErrorMessage = "";

      let fileLength = this.files.length;
      if (event.target.files.length + fileLength > 5) {
        this.fileErrorMessage = this.$t("MAXFILENO");
        return true;
      }
      let fileSize = 0;
      for (var i = 0; i < event.target.files.length; i++) {
        var ext = event.target.files[i].name.split(".").pop();
        if (ext == "sh" || ext == "exe") {
          this.fileErrorMessage = this.$t("CANTNOTUPLOADEXCECUTABLEFILE");
          return true;
        }
        fileSize += Math.round(event.target.files[i].size / 1024);
      }
      if (fileSize > 10240) {
        this.fileErrorMessage = this.$t("MAXFILESIZE");
        return true;
      }

      for (let i = 0; i < event.target.files.length; i++) {
        this.files.push(event.target.files[i]);
      }
    },
    /**
     * @description function to delete the file 
     * @name deleteFile
     */  
    deleteFile(index) {
      this.files.splice(index, 1);
    },
    /**
     * @description function to validate the formData 
     * @name submit
     */      
    async submit() {
      this.$v.messageForm.$touch();
      if (!this.$v.messageForm.$invalid) {
        this.reply();
      }
    },
    async getIconSenderPath(flag){
      let senderIconPath = "";
      if(flag){
        const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMSUPPLIER,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "supplier_no",
              value: this.PayLoad.supplierNo,
            },
          ],
        });
        if(data.success){
          let responseData = data.data;
          if(responseData.length > 0){
            senderIconPath = responseData[0].icon_path ? responseData[0].icon_path : "";
          }
        }
        return senderIconPath;
      }
      else{
        const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADALLMPERSON,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "person_id",
              value: localStorage.getItem("personID"),
            },
          ],
        });
        if(data.success){
          let responseData = data.data;
          if(responseData.length > 0){
            senderIconPath = responseData[0].icon_path ? responseData[0].icon_path : "";
          }
        }
        return senderIconPath;
      }
        
    },
    async getSenderIconPath(){
       if(this.PayLoad.Proposal_no == "" && this.assessment_Details.assesmentno == ""){
         if(this.messageForm.action == "001"){
        this.senderIconPath = this.selectedClientIcon &&
          this.selectedClientIcon != "undefined" &&
          this.selectedClientIcon != "null"
          ? this.selectedClientIcon
          : "";
      }
      else{
        let supplier_flag = localStorage.getItem("supplier_auth");
        let personID = localStorage.getItem("personID");
      if (supplier_flag == "true") {
          const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADWITHJOINMSUPPLIERPERSONINFO,
        {
          limit: 50,
          offset: 0,
          paginate: false,
          fieldlist: [
            "m_supplier_person_info.supplier_person_info_no",
            "m_supplier_person_info.supplier_no",
            "m_supplier_person_info.person_id",
            "m_supplier.supplier_name",
            "m_supplier.icon_path"
          ],
          conditions: [
            {
              key: "m_supplier_person_info.person_id",
              value: personID,
            },
            {
              key: "m_supplier_person_info.del_flg",
              value: "false",
            },
            {
              key: "m_supplier.del_flg",
              value: "false",
            },
          ],
          joins: [
            {
              table: "m_supplier",
              join_type: "left",
              join_conditions: [
                {
                  join_using: "m_supplier.supplier_no",
                  join_operator: "=",
                  join_with: "m_supplier_person_info.supplier_no",
                },
              ],
            },
          ],
        }
      );
      if(data.success){
        let responseData = data.data.data;
            if(responseData.length > 0) {
              this.senderIconPath = responseData[0].icon_path;
            }else{
            this.senderIconPath = "";
            }
          }
          else{
            this.senderIconPath = "";
          }   
        } else {
          this.senderIconPath = await this.getIconSenderPath(false);
        }
      }
      }
      if(this.PayLoad.Proposal_no){
        if(this.messageForm.action == "001"){
        this.senderIconPath = this.selectedClientIcon &&
          this.selectedClientIcon != "undefined" &&
          this.selectedClientIcon != "null"
          ? this.selectedClientIcon
          : "";
      }
      else{
        this.senderIconPath = await this.getIconSenderPath(true);
      }
        
      }
      if(this.assessment_Details.assesmentno){
        if(this.messageForm.action == "001"){
        this.senderIconPath = this.selectedClientIcon &&
          this.selectedClientIcon != "undefined" &&
          this.selectedClientIcon != "null"
          ? this.selectedClientIcon
          : "";
      }else{
        this.senderIconPath = await this.getIconSenderPath(false);
      }
      }
      console.log("HHHHHHHHHHH",this.senderIconPath)
     return this.senderIconPath;
    },
    async getSenderBelongs(){
      if(this.PayLoad.Proposal_no == "" && this.assessment_Details.assesmentno == ""){
         if(this.messageForm.action == "001"){
        this.senderBelongs = this.clientBaseInfo.client_name;
      }
      else{
        let admin_flg = localStorage.getItem("admin_auth");
        let supplier_flag = localStorage.getItem("supplier_auth");
        let wiz_flag = localStorage.getItem("wiz_auth");
        let personID = localStorage.getItem("personID");
      if (supplier_flag == "true") {
          const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADWITHJOINMSUPPLIERPERSONINFO,
        {
          limit: 50,
          offset: 0,
          paginate: false,
          fieldlist: [
            "m_supplier_person_info.supplier_person_info_no",
            "m_supplier_person_info.supplier_no",
            "m_supplier_person_info.person_id",
            "m_supplier.supplier_name",
            "m_supplier.icon_path"
          ],
          conditions: [
            {
              key: "m_supplier_person_info.person_id",
              value: personID,
            },
            {
              key: "m_supplier_person_info.del_flg",
              value: "false",
            },
            {
              key: "m_supplier.del_flg",
              value: "false",
            },
          ],
          joins: [
            {
              table: "m_supplier",
              join_type: "left",
              join_conditions: [
                {
                  join_using: "m_supplier.supplier_no",
                  join_operator: "=",
                  join_with: "m_supplier_person_info.supplier_no",
                },
              ],
            },
          ],
        }
      );
      if(data.success){
        let responseData = data.data.data;
            if(responseData.length > 0) {
              this.senderBelongs = responseData[0].supplier_name;
            }else{
            this.senderBelongs = "";
            }
          }
          else{
            this.senderBelongs = "";
          }   
        } else if (wiz_flag == "true") {
          this.senderBelongs = "WIZ";
        } else if (admin_flg == "true") {
          this.senderBelongs = "システム管理";
        }
      }
      }
      if(this.PayLoad.Proposal_no){
        if(this.messageForm.action == "001"){
        this.senderBelongs = this.clientBaseInfo.client_name;
      }
      else{
        this.senderBelongs = this.PayLoad.Supplier;
      }
        
      }
      if(this.assessment_Details.assesmentno){
        if(this.messageForm.action == "001"){
        this.senderBelongs = this.clientBaseInfo.client_name;
      }else{
        this.senderBelongs = "WIZ";
      }
      }
     return this.senderBelongs;
    },
    /**
     * @description function to register(insert) the form data
     * @name reply
     */  
    async reply() {
      if (this.messageForm.type == "006") {
        this.message_type = "004";
      }
      let admin_flg = localStorage.getItem("admin_auth");
      let supplier_flag = localStorage.getItem("supplier_auth");
      let wiz_flag = localStorage.getItem("wiz_auth");
      let personID = localStorage.getItem("personID");

      if (this.messageForm.action == "001") {
        this.message_sender_account_type = "001";
      } else {
        if (supplier_flag == "true") {
          this.message_sender_account_type = "002";
        } else if (wiz_flag == "true") {
          this.message_sender_account_type = "003";
        } else if (admin_flg == "true") {
          this.message_sender_account_type = "004";
        }
      }

      var formData = new FormData();
      for (let i = 0; i < this.files.length; i++) {
        formData.append("attachments[]", this.files[i]);
      }
      formData.append("client_support_type", this.messageForm.type);
      formData.append("client_no", this.selected_client_data.client_no);

      formData.append(
        "message_dt",
        this.messageForm.customer_response_date.getTime()
      );
      formData.append("client_support_action_type", this.messageForm.action);

      formData.append("message_type", this.message_type);
      formData.append(
        "message_sender_account_type",
        this.message_sender_account_type
      );
      formData.append(
        "message_sender_account_no",
        this.messageForm.action == "001" ? this.client_no : this.client_details.client_account_no
      );
      formData.append(
        "message_sender_person_name",
        this.messageForm.action == "001" ? this.clientBaseInfo.client_person_name : this.client_details.clientPersonName
      );
      formData.append(
        "message_sender_belongs",
        this.getSenderBelongs()
      );

      formData.append("reduction_proposal_no", this.PayLoad.Proposal_no ? this.PayLoad.Proposal_no : 0 );
      formData.append("assess_req_no", this.assessment_Details.assesmentno ? this.assessment_Details.assesmentno : 0);

      formData.append(
        "message_sender_icon_path",
        await this.getSenderIconPath()
      );

      formData.append("message_title", this.messageForm.title);
      formData.append("message_text", this.messageForm.content);

      formData.append(
        "attached_file_flg",
        this.files.length > 0 ? true : false
      );

      formData.append("person_id", personID);

      formData.append("create_id", personID);
      formData.append("update_id", personID);

      this.loading = true;
      this.disabled = true;

      const { data } = await BasicInfoApis.postAPI(
        endPoint.MESSAGECREATECOMMUNICATION,
        formData
      );

      this.loading = false;
      this.disabled = false;

      if (data.success) {
        this.messageForm = { ...defaultForm };
        this.files = [];
        this.$v.$reset();
        this.$router.push({ name: "client_process" });
      } else {
        console.log("REPLY POSTED FAILED");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
#fileSelectBtn label {
  margin-bottom: 0rem;
}
.mx-datepicker {
  width: 169px;
}
.wdtAlign {
  margin-left: -50px;
  width: 130%;
}

.row > .col,
.row > [class*="col-"] {
  padding-right: 5px;
  padding-left: 5px;
}

@media only screen and (max-width: 1200px) {
  .wdtAlign {
    margin-left: 0px !important;
    width: 100% !important;
  }
}
.required-btnAny {
  padding: 2px 5px 2px 5px !important;
  min-width: 10px !important;
  font-size: 11px;
  border-radius: 0px;
  cursor: default !important;
}
.highLightHeaderLabel {
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  padding-top: 9px;
  font-size: initial;
}
.mx-input-wrapper .form-control {
  padding: 7px !important;
  height: calc(1.5em + 0.75rem + 2px);
}
</style>